import React, { useState } from "react";
import styles from "../styles/Chat.module.css";
import Capsule from "../components/Capsule";
import Message from "../components/Message";
import UserMessage from "../components/UserMessage";
import Send from "../asset/send-button.svg";
import { useEffect } from "react";
const content = [
  {
    q: "I am feeling depressed",
    a: [
      "okay,can you go into detail",
      "How can I help you?",
      "It's normal to feel like this.",
    ],
  },
  {
    a: [
      "I had a breakup so I am feeling lost.",
      "Can you tell me what to do to come out of this",
      "Okay",
    ],
  },
];
const Chat = (props) => {
  const [number, setNumber] = useState(0);
  const [messages, setMessages] = useState([content[0].q]);
  const [selected, setSelected] = useState([false, false, false]);
  const [results, setResults] = useState(false);
  const [solutions, setSolutions] = useState(false);
  const optionClicked = (index) => {
    const data = [false, false, false];
    data[index] = true;
    setSelected(data);
  };
  useEffect(() => {
    if (number === 1) {
      setTimeout(() => {
        setResults(true);
        console.log("inside");
      }, 2000);
    }
  }, [number]);
  const sendHandler = () => {
    const index = selected.findIndex((ele) => ele === true);
    if (index !== -1) {
      if ((messages.length % 2 !== 0) & (number < 2)) {
        const dataa = messages.concat(content[number].a[index]);
        console.log(dataa);
        setMessages(dataa);
        setNumber((state) => state + 1);

        setTimeout(() => {
          const newData = dataa.concat(content[number + 1].a[index]);
          if (index === 0) {
            setSolutions(true);
          }
          console.log(newData);
          setMessages(newData);
        }, 1000);
      }
    }
  };
  return (
    <div>
      <div className={styles.wrapper}>
        {results ? (
          <div className={styles.resultsWrapper}>
            <h1>Results</h1>
            {solutions ? (
              <h2 className={styles.results}>
                Your answer is <strong>correct</strong> and the reason is that
                the client has not yet opened up about the root cause of problem
              </h2>
            ) : (
              <h2 className={styles.results}>
                Your answer is wrong <br /> <br />
                The correct answer is "Okay,can you go into details" because the
                client has not yet opened about the root cause of the problem.
              </h2>
            )}
          </div>
        ) : (
          <div>
            <div className={styles.messages}>
              {messages.map((ele, index) => {
                if (index % 2 === 0) {
                  return <Message content={ele} />;
                } else {
                  return <UserMessage content={ele} />;
                }
              })}
            </div>
            <div className={styles.optionsBox}>
              {number < 1
                ? content[number].a.map((ele, index) => (
                    <Capsule
                      clicked={() => optionClicked(index)}
                      selected={selected[index]}
                    >
                      {ele}
                    </Capsule>
                  ))
                : null}
              <div className={styles.space}></div>
            </div>
            <button className={styles.button} onClick={sendHandler}>
              <img src={Send} alt="send" className={styles.image} />
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default Chat;
